body {
  background-color: rgba(0, 0, 0, 0.631);
}

.App {
  text-align: center;
}
.highlight {
  background-color: yellow;
}
.error {
  background-color: lightcoral;
  border: red;
}
@font-face {
  font-family: myFont;
  src: url(/Lora-Italic-VariableFont_wght.ttf);
}
@font-face {
  font-family: secFont;
  src: url(/Lora-VariableFont_wght.ttf);
}
h1 {
  font-family: myFont;
}

input {
  border: 1px solid red;
  border-radius: 7px;
  margin-left: 40%;
  margin-top: 20px;
  text-align: center;
}

input::placeholder {
  font-weight: bolder;
  opacity: 0.5;
  color: green;
  margin-left: 30%;
}
button {
  background-color: rgb(163, 7, 38);
  cursor: pointer;
}
p {
  color: rgb(255, 242, 0);
  font-size: 40px;
  font-family: secFont;
}
